<script>
import Service from './service'
import moment from 'moment'
import config from '@/config'

export default {
  components: {  },
  props: [],
  data() {
    return {
      bgObj: {
        backgroundColor: '#f5f7fa'
      },
      tooltip: {
        trigger: 'item',
        formatter: '{c} ({d}%)'
      },
      grid: {
        right: '5%',
        left: '5%'
      },
      start:"",
      end:"",
      value1: [],
      itemList: [],
      houseItemList: [],
      pieItemList: [],
      chartData: {},
      tableData: [],
      pieChartData: {},
      pieTableData:[],
      lineData: {},
      printingId: '',
      showType: '',
      chartSettings: {
        smooth: true
      },
      areaData: []
    }
  },
  methods: {
    totalFormatter(row, column){
        return row.todayNumber+row.todayNumber2+row.todayNumber3+row.todayNumber4;
    },
    downT() {
        let dd=this.value1
        console.log(dd)
        let params = {
            start: dd[0],
            end:dd[1]
        }
        let url = config.LOCAL + '/system/summary/download?' + this.dealGetParams(params)
        console.log(url)
        window.open(url)
    },
    downT1() {
        let url = config.LOCAL + '/system/summary/house/download?'
        console.log(url)
        window.open(url)
    },
    downT2() {
        let url = config.LOCAL + '/system/summary/pie/download?'
        console.log(url)
        window.open(url)
    },
    dealGetParams(obj) {
        let arr = []
        if (obj instanceof Object) {
            Object.keys(obj).forEach(item => {
                arr.push(`${item}=${obj[item]}`)
            })
        }
        return arr.join('&')
    },
    handleSearch(){
        //this.getItemCard()
        this.getItemNewCard()
        this.getItemHouse()
        this.getItemPie()
    },
    getItemCard() {
      Service.getItemCard({}).then(res => {
        this.itemList = res.data
      })
    },
      getItemNewCard() {
          let dd=this.value1
          console.log(dd)
          let params = {
              start:dd[0],
              end:dd[1]
          }
          Service.getItemNewCard(params).then(res => {
              this.itemList = res.data
          })
      },
    getItemHouse() {
        let dd=this.value1
        console.log(dd)
        let params = {
            start:dd[0],
            end:dd[1]
        }
        Service.getItemHouse(params).then(res => {
            this.houseItemList = res.data
        })
    },
    getItemPie() {
        let dd=this.value1
        console.log(dd)
        let params = {
            start: dd[0],
            end:dd[1]
        }
        Service.getItemPie(params).then(res => {
            this.pieItemList = res.data
        })
    },
    getPieChart() {
      Service.getPieChart({}).then(res => {
        this.chartData = res.data
      })
    },
    getHouseTable() {
      Service.getHouseTable({}).then(res => {
        this.tableData = res.data
      })
    },
    getPieChartData() {
        Service.getPieChartData({}).then(res => {
            this.pieChartData = res.data
        })
    },
      getPieTableData() {
        Service.getPieTableData({}).then(res => {
            this.pieTableData = res.data
        })
    },
    getLineChart() {
      Service.getLineChart({}).then(res => {
        this.lineData = res.data
      })
    },
    getAreaTable() {
      Service.getAreaTable({}).then(res => {
        this.areaData = res.data
      })
    },
    gotoFeedback(name,tab) {
        let dd=this.value1
        let params= {
                type: name,
                start: dd[0],
                end: dd[1],
                tab: tab
        }
        let url = '/system/summary/mapCount/mapPage?' + this.dealGetParams(params)
        console.log(url)
        window.open(url)
    },
    showPrintingDialog() {
        let dd=this.value1
        console.log(dd)
        let params = {
            start:dd[0],
            end:dd[1],
            dy:"总数"
        }
        this.showType = 'print'
        this.printingId = params
        this.$refs.printingDialog.show()
    },
    showPrintingDialog2() {
        let params = {
            dy:"小区房屋统计"
        }
        this.showType = 'print'
        this.printingId = params
        this.$refs.printingDialog.show()
    },
    showPrintingDialog3() {
        let params = {
            dy:"小区人口统计"
        }
        this.showType = 'print'
        this.printingId = params
        this.$refs.printingDialog.show()
    },
  },
  mounted() {
    let end = moment().format("YYYY-MM-DD 23:59:59")
    let start =moment().subtract(1,"month").format("YYYY-MM-DD 00:00:00")
    console.log(end)

    this.value1=[start,end]
    //this.getItemCard()
    this.getItemNewCard()
    this.getItemHouse()
    this.getItemPie()
    this.getPieChart()
    this.getPieChartData()
    this.getPieTableData()
    this.getHouseTable()
    this.getLineChart()
    this.getAreaTable()
  }
}
